<script>
import Grafico from "@/components/Grafico.vue";
import { ref } from "@vue/reactivity";
import { useStore } from "vuex";
import addDate from "date-fns/add";
import lightFormat from "date-fns/lightFormat";
import { computed } from "@vue/runtime-core";

export default {
  components: { Grafico },
  props: {},

  setup() {
    const store = useStore();

    const hoy = lightFormat(addDate(new Date(), { days: 0 }), "yyyy-MM-dd");
    const ayer = lightFormat(addDate(new Date(), { days: -1 }), "yyyy-MM-dd");

    const array = computed(() => {
      return store.state.arrayGraficos;
    });

    const filtros = ref({
      destino: "TODOS",
      bitacora: "TODOS",
      parametro: "TODOS",
      tipoEquipo: "TODOS",
      fechaInicio: ayer,
      fechaFin: hoy,
      palabra: "",
    });

    const arrayGraficos = async () => {
      try {
        const response = await store.dispatch("getConexion", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "conexion",
        });

        if (response) {
          const data = await store.dispatch("getArrayGraficos", {
            idDestino: localStorage.getItem("idDestino"),
            idUsuario: localStorage.getItem("usuario"),
            fechaInicio: filtros.value.fechaInicio,
            fechaFin: filtros.value.fechaFin,
            action: "graficos",
          });

          if (data.response) array.value = data.data;
        }
      } catch (error) {
        console.log(error);
      }
    };
    arrayGraficos();

    return { array, filtros };
  },
};
</script>
