import { createRouter, createWebHashHistory } from 'vue-router'

import Misbitacoras from '../views/Misbitacoras.vue'
import Bitacorashistorico from '../views/Bitacorashistorico.vue'
import Bitacorasgraficas from '../views/Bitacorasgraficas.vue'
import MisBitacorasCapturas from '../views/MisBitacorasCapturas.vue'

const routes = [
  {
    path: "/:catchAll(.*)",
    name: 'Misbitacoras',
    component: Misbitacoras
  },

  {
    path: '/historico',
    name: 'Bitacorashistorico',
    component: Bitacorashistorico
  },

  {
    path: '/graficos',
    name: 'Bitacorasgraficas',
    component: Bitacorasgraficas
  },

  {
    path: '/MisBitacorasCapturas',
    name: 'MisBitacorasCapturas',
    component: MisBitacorasCapturas
  },

]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
